          <p class="nickname">{{realName || $config.name}}</p> 
        </div>
        <div class="flex-mid">
            <p class="nickname">{{userLevelName || '游客' }}</p>
        </div>  
        <div class="flex">
          <p :class="userLevel > 2?'role':''" style="color:#333;" @click="toApply">{{userLevel > 2? '合作商': '申请入驻'}}</p>
