          <p class="num">0.00</p>
          <p class="text">拼团收益(元)</p>
        </div>-->
      <!-- </div> -->
    </div>
    <div  class="com">
        <van-panel> 
            <van-cell class="item" id="fil_time_1" @click="$router.push({ name: 'bonus', query: { code: '1205414939127541550',name: '应付款' } })">
              <p  v-for="(item,index) in accountList" :key="index" ><span class="price" v-if="item.ptCode=='1205414939127541550' ">{{item.acAmt}}</span></p> 
              <span>红包</span>
            </van-cell>
            <van-cell class="item" id="fil_time_2" @click="$router.push({ name: 'bonus', query: { code: '1202763465547927552',name: '佣金结算' } })">
              <p  v-for="(item,index) in accountList" :key="index"><span class="price" v-if="item.ptCode=='1202763465547927552' ">{{item.acAmt}}</span></p>
              <span>佣金</span>
            </van-cell>
            <van-cell class="item" id="fil_time_3"  @click="$router.push({ name: 'bonus', query: { code: '1202763496896155648',name: '已提现' } })">
              <p  v-for="(item,index) in accountList" :key="index"><span class="price" v-if="item.ptCode=='1202763496896155648' ">{{item.acAmt}}</span></p>
              <span>已提现</span>
            </van-cell>
            <van-cell class="item" id="fil_time_4"  @click="$router.push({ name: 'bonus', query: { code: '1202763496896155648',name: '提现待批' } })">
              <p  v-for="(item,index) in accountList" :key="index"><span class="price" v-if="item.ptCode=='1202763496896155648' ">{{item.acAmt}}</span></p>
              <span>提现待批</span>
            </van-cell>   
        </van-panel> 
