        <div class="list" style="text-align:left;">
          <div class="item" v-for="(item,index) in list" :key="index" @click="to(item.router)">
            <img :src="item.icon" v-if="(userLevel >= item.userLevel && item.text != '我的团队') || (item.text=='我的团队' && myTeamShow == '1')" alt />
            <p v-if="(userLevel >= item.userLevel && item.text != '我的团队') || (item.text=='我的团队' && myTeamShow == '1')">{{item.text}}</p>
          </div>
        </div>
